'use client';

import React, { useEffect, useRef } from 'react';
import mapboxgl from 'mapbox-gl';
import 'mapbox-gl/dist/mapbox-gl.css';

mapboxgl.accessToken = 'pk.eyJ1IjoiZGVsNHNhbiIsImEiOiJjbWhsejg1Y24xbmhtMmxzaDIzbnZtdW15In0.mAUSQhUmaQKIG43x9pjAuQ'; // replace with your actual token

export default function MapStream() {
  const mapContainerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (!mapContainerRef.current) return;

    const map = new mapboxgl.Map({
      container: mapContainerRef.current,
      style: 'mapbox://styles/mapbox/streets-v12',
      center: [-0.186964, 5.6836],
      zoom: 7,
    });

    map.on('load', () => {
      console.log('✅ Map loaded');
    });

    map.on('error', (e) => {
      console.error('❌ Mapbox error:', e.error);
    });

    return () => {
      map.remove();
    };
  }, []);

  return (
    <div className="relative h-screen w-screen">
      <div
        ref={mapContainerRef}
        className="absolute inset-0 z-0"
        style={{ touchAction: 'auto' }}
      />
      <div className="absolute top-4 left-4 z-10 bg-white p-4 rounded shadow">
        <h1 className="text-xl font-bold mb-2">InsightHubGH</h1>
        <p className="text-sm text-gray-600">Map baseline loaded.</p>
      </div>
    </div>
  );
}